@use "global" as *;

.floating-banner {
  position: relative;
  width: 100%;
  max-width: rem(200);
  position: fixed;
  bottom: rem(16);
  right: rem(32);
  z-index: 9999;
  transition: 0.3s;
  cursor: pointer;
  background: #FFFFFF; //ホバー時に画像が透過して白っぽくなる
}
.floating-banner_img:hover {
  opacity: 0.8;
}
.floating-banner_img {
  width: 100%;
  box-shadow: 0 10px 25px 0 rgba(0, 0, 0, .5);
}



.round_btn {
  display: block;
  position: relative;
  width: rem(20);
  height: rem(20);
  border: 1px solid #333; /* 枠の調整 */
  border-radius: 50%;  /* 丸みの度合い */
  background: #FFFFFF; /* ボタンの色 */
  box-shadow: 0 10px 25px 0 rgba(0, 0, 0, .5);
}
 
.round_btn::before, .round_btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1px; /* 棒の幅（太さ） */
  height: rem(12); /* 棒の高さ */
  background: #000; /* バツ印の色 */
}
 
.round_btn::before {
  transform: translate(-50%,-50%) rotate(45deg);
}
 
.round_btn::after {
  transform: translate(-50%,-50%) rotate(-45deg);
}


.close-btn:hover {
  cursor: pointer;
  opacity: 0.8;
}

/* checkbox non-display */
.checkbox {
  display: none;
}

/* close button */
.action-close .close-btn {
  position: absolute;
  right: -35px;
}
.action-close #close:checked ~ .close-btn {
  display: none;
}
.action-close #close:checked ~ .box {
  display: none;
}